.navbar {
  background-color: rgb(31, 31, 34);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  box-shadow: -2px -2px 6px rgba(255, 255, 255, .1),
               4px 4px 12px rgba(0, 0, 0, .8);
}
  
  .navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
  }
  
  .container {
    z-index: 1;
    width: 100%;
    max-width: 1300px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 50px;
    padding-left: 50px;
  }
  
  .navbar-logo {
    margin-right: 0.5rem;
    color: #6f6f6f;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    width: 50px;
    height: 50px;
    justify-content: center;
    background-color: rgb(31, 31, 34);
    border-radius: 50%;
    box-shadow: -1px -1px 3px rgba(255, 255, 255, .1),
                2px 2px 6px rgba(0, 0, 0, .8);

    &:hover {
      color: #6ee6fc;
      box-shadow: -1px -1px 3px rgba(255, 255, 255, .1),
                  2px 2px 6px rgba(0, 0, 0, .8),
              inset -2px -2px 10px rgba(255, 255, 255, .05),
              inset 2px 2px 10px rgba(0, 0, 0, .5);
              transition: all 0.5s ease;

              & svg {
                fill: #6ee6fc;
                transition: all 0.5s ease;
              }
    }
  }
  
  .nav-menu {
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    text-align: center;
    justify-content: flex-end;
  }
  
  .nav-item {
    border-bottom: 2px solid transparent;
    position: relative;
    background-color: rgb(31, 31, 34);
    text-align: center;
    margin: 0 10px;
    color: #6f6f6f;
    font-size: 20px;
    border-radius: 6px;
    cursor: pointer;
    box-shadow: -2px -2px 6px rgba(255, 255, 255, .1),
                4px 4px 12px rgba(0, 0, 0, .8);
  }
  
  .nav-item:hover {
    color: #6ee6fc;
    box-shadow: -2px -2px 6px rgba(255, 255, 255, .1),
                 4px 4px 12px rgba(0, 0, 0, .8),
            inset -4px -4px 10px rgba(255, 255, 255, .05),
            inset 4px 4px 10px rgba(0, 0, 0, .5);
            transition: all 0.5s ease;
  }
  
  .nav-links {
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    color: inherit;

    :hover {
      color: #6ee6fc;
      transition: all 0.3s ease;
    }
    
    &--github svg {
      fill: #6ee6fc;
      font-size: 25px;
    }
  }
  
  .fa-bars {
    color: #6f6f6f;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background-color: rgb(31, 31, 34);
      left: 0;
      opacity: 1;
      transition: all 0.6s ease;
      z-index: 9999;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      color: #6ee6fc;
      transition: all 0.3s ease;
    }
  
    .nav-item:hover {
      border: none;
    }
  
    .nav-item {
      width: 100%;
    }
  
    .navbar-logo {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(25%, -50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #6ee6fc;
      font-size: 2rem;
    }
  
    .nav-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 120px;
    }
  }